body {
  background-color: #e0e0e0;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 14px; }

.container {
  max-width: 486px;
  margin: 15% auto 0;
  position: relative; }

.tab-panel {
  box-sizing: border-box;
  display: none;
  justify-content: space-between;
  flex-wrap: wrap;
  background-color: #ffffff;
  max-width: 486px;
  min-height: 280px;
  padding: 15px;
  border-radius: 0 5px 5px 5px;
  border: 1px solid rgba(0, 0, 0, 0.2); }

.tab-panel.active {
  display: flex; }

.simplebar-scrollbar {
  right: 0 !important;
  width: 4px !important;
  height: 70px !important;
  background-color: #1575b1 !important;
  border-radius: 5px !important; }

.simplebar-vertical {
  width: 4px !important;
  border-radius: 5px !important;
  background-color: #ededed !important; }

.simplebar-scrollbar:before {
  background-color: #1575b1 !important; }

input[type="email"] {
  color: #1474ae;
  text-decoration: underline; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0; }

.nav-tabs {
  position: relative;
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  top: 1px; }

.nav-tabs__item {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px; }

.nav-tabs__link {
  display: flex;
  align-items: center;
  background: linear-gradient(to top, #2069ad, #0684b4);
  color: #ffffff;
  height: 32px;
  text-decoration: none;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  padding: 0 15px; }

.nav-tabs__link:hover {
  color: #ffffff; }

.nav-tabs .nav-tabs__link.active {
  background: #ffffff;
  color: #000000; }

.nav-tabs__item:last-child {
  display: block;
  margin-left: 10px; }

.nav-tabs .nav-tabs__link {
  border: 0; }

.page-photo,
.page-info-wrap {
  display: inline-block; }

.page-info-wrap {
  width: 300px; }

.page-photo__avatar {
  width: 132px;
  height: 170px;
  background-image: url("../../img/me.jpg");
  background-repeat: no-repeat;
  background-size: 170px;
  border-radius: 5px;
  box-shadow: inset 0 0 10px 1px rgba(0, 0, 0, 0.5); }

.page-photo__add-friend {
  box-sizing: border-box;
  width: 132px;
  height: 30px;
  padding: 5px 10px;
  font-size: 12px;
  margin-top: 5px;
  color: #ffffff;
  border-radius: 2px;
  border: 0;
  box-shadow: 0 1.5px 0 #b6b6b6;
  background: linear-gradient(to top, #e75200, #e68000); }

.page-info-wrap__name {
  border: 0;
  color: #000000;
  background-color: #ffffff;
  font-size: 12px;
  font-size: 20px; }

.page-info-wrap__city {
  border: 0;
  color: #000000;
  background-color: #ffffff;
  font-size: 12px;
  color: #757575;
  font-size: 11px;
  margin-bottom: 20px; }

.page-info-wrap__contact {
  border: 0;
  color: #000000;
  background-color: #ffffff;
  font-size: 12px; }

.page-info-wrap__description {
  color: #000000;
  font-weight: 600;
  font-size: 12px;
  float: left; }

.page-info-wrap__field-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px; }

.page-info-wrap__hobbies-field {
  margin-top: 30px;
  margin-bottom: 0; }
  .page-info-wrap__hobbies-field input {
    width: 110px; }

.page-info-wrap__add-hobbies {
  font-size: 12px;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  color: #1474ae;
  cursor: pointer; }

.page-info-wrap__add-hobbies:hover {
  background-color: #f0f0f0; }

.page-info-wrap__hobbies-list {
  display: block;
  list-style: none;
  padding-left: 0;
  margin: 0; }
  .page-info-wrap__hobbies-list li {
    display: inline-block;
    margin-right: 5px;
    margin-top: 5px;
    padding: 0 5px;
    background: #fff;
    border: 1px solid #d4d4d4;
    border-radius: 2px;
    color: #1474ae;
    cursor: pointer; }
  .page-info-wrap__hobbies-list li:hover {
    background-color: #f0f0f0; }

@media only screen and (max-width: 512px) {
  .page-photo {
    margin: 0 auto;
    padding: 0 20px 20px 20px; } }

.friends-wrap {
  width: 100%;
  height: 250px; }

.friends-row {
  display: flex; }

.friends-row + .friends-row {
  margin-top: 30px; }

.friends-row__avatar {
  width: 62px;
  height: 62px;
  border-radius: 2px;
  float: left;
  margin-right: 20px;
  object-fit: cover; }

.friends-row__link {
  color: #1575b1;
  font-size: 12px;
  font-weight: 600;
  text-decoration: underline;
  margin-bottom: 2px; }

.friends-row__city {
  font-size: 10px; }

.friends-row__status {
  display: flex;
  align-items: flex-end;
  font-style: italic;
  font-size: 10px;
  color: #6aa666;
  height: 100%; }

.friends-row__info {
  display: flex;
  flex-direction: column;
  height: 100%; }

.friends-row__user {
  width: 100%; }

@media only screen and (max-width: 512px) {
  .friends-wrap {
    height: 100%;
    max-height: 400px; }
  .friends-row {
    display: flex;
    flex-wrap: wrap; }
  .friends-row__user {
    margin-bottom: 20px; }
  .friends-row + .friends-row {
    margin-top: 0; } }
